var estilo_ponto = function (feature) {

  let valor = feature.get('tt_rural');
  let raio = 50 * (valor / 13) ^ 0.57;

  let texto = new Text({
    font: '13px Calibri,sans-serif',
    fill: new Fill({ color: '#000', }),
    stroke:
      new Stroke({
        color: '#fff',
        width: 4,
      }),
    offsetX: 10,
    offsetY: 10,
    text: valor.toString()
  });


  let pointStyle = new Style({
    image: new CircleStyle({
      radius: raio,
      fill: new Fill({ color: '#7c120e' }),
      stroke: new Stroke({ color: '#5c5c5c', width: 2 }),
    }),
    text: texto,
  });

  if (valor != 0) {
    return pointStyle;
  }
};



